.react-tags {
    position: relative;
    padding: 0.8rem;
    border: 1px solid #D1D1D1;
    border-radius: 1px;
    font-size: 1em;
    line-height: 1.2;
    cursor: text;
    background-color: white;
    margin-bottom: 1rem;
}

.react-tags__selected {
    display: inline;
}

.react-tags__selected-tag {
    display: inline-block;
    box-sizing: border-box;
    margin: 0 6px 6px 0;
    padding: 6px 8px;
    border: 1px solid #D1D1D1;
    border-radius: 2px;
    background: #F1F1F1;
    outline: none;
    /* match the font styles */
    font-size: inherit;
    line-height: inherit;
}

.react-tags__selected-tag:after {
    content: '\2715';
    color: #AAA;
    margin-left: 8px;
}

.react-tags__selected-tag:hover,
.react-tags__selected-tag:focus {
    outline: none;
}

.react-tags__search {
    display: inline-block;
    width: 100% !important;
    position: relative;

}


@media screen and (min-width: 30em) {

    .react-tags__search {
        /* this will become the offsetParent for suggestions */
        position: relative;
    }

}

.react-tags__search input {
    width: 100% !important;
    margin: 0;
    padding: 0;
    border: 0;
    outline: none;
    border-width: 0px !important;
    /* match the font styles */
    font-size: inherit;
    line-height: inherit;
    padding-left:2px !important;
}

.react-tags__search input::-ms-clear {
    display: none;
}

.react-tags__suggestions {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
}

@media screen and (min-width: 30em) {

    .react-tags__suggestions {
        width: 240px;
    }

}

.react-tags__suggestions ul {
    margin: 4px -1px;
    padding: 0;
    list-style: none;
    background: white;
    border: 1px solid #D1D1D1;
    border-radius: 2px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.react-tags__suggestions li {
    border-bottom: 1px solid #ddd;
    padding: 6px 8px;
}

.react-tags__suggestions li mark {
    text-decoration: underline;
    background: none;
    font-weight: 600;
}

.react-tags__suggestions li:hover {
    cursor: pointer;
    background: #eee;
}

.react-tags__suggestions li.is-active {
    background: #b7cfe0;
}

.react-tags__suggestions li.is-disabled {
    opacity: 0.5;
    cursor: auto;
}
.statBox{
    background-color:white;
    border:1px solid #cacaca;
    padding: 1rem;
    text-align: center;
    margin-top: 1.7rem;
    border-left: 5px solid #226192;
}
.statCount{
    font-size: 3rem;
    font-weight: bold;
}
.statText{

}
.totalIterations{
    font-style: italic;
    margin-top: 0.5rem;
}

.ui.form .disabled{
    opacity: 1 !important;
}
.ui.form .field.disabled>label, .ui.form .fields.disabled>label{
    opacity: 1 !important;
}
.inputBox{
    width: 100%;
    padding: 0px 5px 0px 5px;
}
.statsGraph{
    background-color: white;
    margin-top: 0.8rem;
    padding: 1rem;
    border:1px solid lightgrey;
}
.tagsMain{
    background-color: white;
    padding: 1rem;
    border: 1px solid #cccccc;
}
.tagBody{
    background-color: #f2f2f2;
    border: 1px solid #cccccc;
    padding: 0.3rem 0.5rem;
    margin: 0rem 0.5rem 0.5rem 0rem;
}

.tagText, .tagClose, .tagBody{
    display: inline-block;
}
.tagClose{
    margin-left: 0.5rem;
}
.tagClose:hover{
    cursor: pointer;
}
.tagsMain input{
    border-width: 0px !important;
    padding-left: 0px !important;
}